
import { computed, defineComponent, PropType } from "vue";
import { alertConfirmation } from "@/utils/Alerts";
import dayjs from "dayjs";
import { DISPLAY_DATE_FORMAT } from "@/utils/Date";
import { DataTable, RowActionButtonInterface, TableColumnInterface, TableConfigInterface } from '@uniquedj95/vtable'

export default defineComponent({
  name: "OutcomesTable",
  props: {
    patientStates: {
      type: Array as PropType<Array<any>>,
      required: true
    },
  },
  components: {
    DataTable,
  },
  emits: ["voidOutcome"],
  setup(props, { emit }) {
    const columns: TableColumnInterface[] = [
      { path: 'name', label: "Outcome"},
      { path: 'start_date', label: "Start Date", formatter: (v) => dayjs(v).format(DISPLAY_DATE_FORMAT) },
      { path: 'end_date', label: "End Date", formatter: (v) => dayjs(v).format(DISPLAY_DATE_FORMAT) },
    ];

    const tableConfig: TableConfigInterface = {
      showSearchField: false,
      showSubmitButton: false
    }

    const rows = computed<any[]>(() => props.patientStates.map((state: any, index: number) => ({
      ...state,
      index
    })));

    const TableRowActions: RowActionButtonInterface[] = [
      { label: "void", color: 'danger', action: async (row) => {
        if((await alertConfirmation("Are you sure you want to void this outcome?"))){
          emit("voidOutcome", {stateId: row.patient_state_id, index: row.index});
        }
      }}
    ]

    return {
      rows,
      columns,
      tableConfig,
      TableRowActions,
    };
  }
})
