
import { alertConfirmation } from "@/utils/Alerts";
import EventBus from "@/utils/EventBus";
import { IonGrid, IonRow, IonCol, IonButton } from "@ionic/vue";
import dayjs from "dayjs";
import { defineComponent, reactive } from "vue";
import { DTForm } from "../interfaces/dt_form_field";
import { EmcEvents } from "../interfaces/emc_event";
import { submitForm } from "../utils/form";
import DateInput from "./inputs/DateInput.vue";

export default defineComponent({
  name: "EnrollmentForm",
  components: {
    DateInput,
    IonGrid,
    IonRow,
    IonCol,
    IonButton,
  },
  props: ["birthdate"],
  emits: ["enrollProgram"],
  setup(props, { emit }) {
    const today = dayjs().format("YYYY-MM-DD");
    const form = reactive<DTForm>({
      date: {
        value: "",
        label: "Date of Enrollment",
        required: true,
        validation: async (date) => {
          if(new Date(date.value) < new Date(props.birthdate)) return ["Date of enrollment cannot be before date of birth"]
          if(new Date(date.value) > new Date(today)) return ["Date of enrollment cannot be in the future"]
          return null
        }
      },
    })

    const onReset = async () => {
      if((await alertConfirmation("Are you sure you want to clear all fields"))){
        form.date.value = "";
        form.date.error = "";
        EventBus.emit(EmcEvents.ON_CLEAR)
      }
    }

    const enrollProgram = async () => submitForm(form, ({ date }) => emit("enrollProgram", date))

    return {
      form,
      today,
      onReset,
      enrollProgram,
    }
  }
})
